import { createStore } from "redux";

const initialState = {
  Links: []
};

function Reducer(state = initialState, action) {
  switch (action.type) {
    case "ADD_URL":
      let linksArr = state.Links;
      if (!linksArr.includes(action.payload))
        linksArr = [action.payload].concat(linksArr);

      return { ...state, Links: linksArr };
    case "REMOVE_URL":
      return {
        ...state,
        Links: state.Links.filter(url => url !== action.payload)
      };
    default:
      return state;
  }
}

const Store = createStore(Reducer);

export default Store;
