import React from "react";
import { connect } from "react-redux";

import { addURL, removeURL } from "./actions";

class CreatePlaylist extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      url: ""
    };
  }
  onChange = e => {
    this.setState({ url: e.target.value });
  };
  addPlaylist = e => {
    e.preventDefault();
    const addURLAction = addURL(this.state.url);
    this.props.dispatch(addURLAction);
    this.setState({ url: "" });
  };

  getVideoId = () => {
    const links = this.props.Links;
    const linkId =
      links.length && links[0].substring(links[0].indexOf("=") + 1);
    return linkId;
  };

  getVideoLink = () => {
    return this.props.Links.length ? this.props.Links[0] : "";
  };

  handleRemoveClick = event => {
    event.preventDefault();

    const removeURLAction = removeURL(event.target.dataset.link);
    this.props.dispatch(removeURLAction);
  };

  render() {
    return (
      <React.Fragment>
        <form onSubmit={this.addPlaylist}>
          <input
            type="url"
            style={{ height: "30px", width: "30%" }}
            placeholder="Enter-YouTube-Url"
            pattern="https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,4}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)"
            value={this.state.url}
            onChange={this.onChange}
          />
          <button type="submit" style={{ height: "36px" }}>
            Add
          </button>
        </form>
        <div>
          <h1 style={{ color: "silver" }}> YouTube-Playlist</h1>
          <hr style={{ width: "34%" }} />
          <ul>
            {this.props.Links.map((link, index) => (
              <li key={link + index.toString()}>
                {index === 0 ? "Playing Now" : " Coming up " + index.toString()}
                , {link}{" "}
                <span>
                  <a data-link={link} href="#" onClick={this.handleRemoveClick}>
                    X
                  </a>
                </span>
              </li>
            ))}
          </ul>
        </div>
        <div>
          <h1 style={{ color: "silver" }}>VideoPlayer</h1>
          <button
            data-link={this.getVideoLink()}
            onClick={this.handleRemoveClick}
          >
            Play Next Video
          </button>
          <iframe
            width="420"
            height="345"
            src={`https://www.youtube.com/embed/${this.getVideoId()}`}
          ></iframe>
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return state;
};

export default connect(mapStateToProps)(CreatePlaylist);
